.background-container {
  background-image: url("../Photos/SignUpBg.webp");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Grabmart {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.Grabmart i {
  position: absolute;
  right: 100px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 45px;
  color: white;
}

.Grabmart h1 {
  color: white;
  font-size: 40px;
  font-weight: bold;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.LoginPage {
  width: 500px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 30px 40px;
}

.LoginPage h1 {
  font-size: 36px;
  text-align: center;
  color: black;
}

.LoginPage .input-box {
  position: relative;
  width: 100%;
  height: 50px;
  margin: 25px 0;
}

.input-box input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: 2px solid black;
  border-radius: 40px;
  font-size: 16px;
  color: black;
  padding: 20px 45px 20px 20px;
  outline: none;
}

.input-box input::placeholder {
  color: black;
}

.input-box i {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
}

.remember-forgot {
  display: flex;
  justify-content: flex-end;
  font-size: 14.5px;
  margin: -10px 0px 20px;
}

.remember-forgot a {
  color: black !important;
  text-decoration: none;
}

.remember-forgot a:hover {
  text-decoration: underline;
}

.LoginPage .button {
  width: 100%;
  height: 45px;
  background-color: black;
  border: none;
  border-radius: 40px;
  cursor: pointer;
  font-size: 16px;
  color: white;
  font-weight: 600;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.LoginPage .button:hover {
  background-color: rgb(50, 50, 50);
}

.LoginPage .register-link {
  font-size: 14.5px;
  text-align: center;
  margin: 20px 0px 15px;
}

.register-link p a {
  color: black;
  text-decoration: none;
  font-weight: 600;
}

.register-link p a:hover {
  text-decoration: underline;
}

.error-message {
  color: white;
  font-size: 18px;
  text-align: center;
  margin-top: 10px;
  font-family: "Poppins", sans-serif;
}
